@import "libs/imports";
@import "layouts/base-layout";

* {
  box-sizing: border-box;
}

::selection {
  background-color: var(--light-blue-50p);
  color: var(--white);
}

hr {
  border-color: var(--text-talee-title);
  opacity: 0.2;
}

.input {
  &__lable {
    color: var(--gray);
  }

  &__input {
    display: block;
    width: 100%;
    border: none;
    background-color: transparent;
    outline: none;
    height: 23px;
    line-height: 23px;
    font-size: 23px;
    border-bottom: 1px solid var(--light-blue);
  }
}

a {
  /* (love hate) LVHA (link visited hover active) */
  text-decoration: none;
  color: black;

  &:link {
    color: black;
  }

  &:visited {
    color: black;
  }

  &:hover {
    // text-decoration: underline;
  }

  &:active {
    color: black;
  }
}

.to-up {
  @include flex-center;
  @include size(60px);

  padding: 10px;
  transition: all 0.2s;
  opacity: 0.3;
  border: 1px solid transparent;
  border-radius: 100%;

  &__image {
    height: 100%;
  }

  @include onLabtop {
    &:hover {
      border: 1px solid var(--prinary-50p);
      opacity: 1;
    }

    &:active {
      transform: rotateX(30deg);
      filter: drop-shadow(0 30px 10px var(--dark-blue));
    }
  }

  &--close {
    @include no-event;
    opacity: 0;
    transform: translateX(20px) scale(0.9);
  }

  @include toLabtop {
    @include size(46px);
    padding: 4px;
  }
}

.to-link {
  font-weight: 500;

  &:hover {
    text-decoration: underline;
  }
}

// ---

._fix-hook {
  padding-top: 100px;
  margin-top: -100px;
}

._fix-for-decor {
  @include toLabtop {
    padding-right: 30px;
  }

  @include toPhone {
    padding: 0;
  }
}

._no-event {
  @include no-event;
}

._relative {
  position: relative;
}

._absolute {
  position: absolute;
}

