// /* Decores */

.decor-circle-border {
  width: 764px;
  height: 744px;
  position: absolute;
  z-index: 0;
  border: 1px solid var(--light-blue);
  border-radius: 100%;
  top: -162px;

  @include toDesktop {
    @include size(554px);
    top: -126px;
  }

  @include toPhablet {
    @include size(687px);
    top: -160px;
  }

  @include toPhone {
    @include size(384px);
  }
}

.decor-shadow-gradient {
  position: relative;

  & * {
    z-index: 1;
  }

  &::before {
    content: "";
    position: absolute;
    inset: 0;
    pointer-events: none;
    z-index: 0;

    filter: blur(150px);
    background: linear-gradient(
      360deg,
      rgb(150 182 220 / 23%) 0%,
      rgb(166 239 221 / 23%) 100%
    );
  }
}

// /* Images Components */

.img-quotes {
  display: block;
  width: 86px;
  height: 74px;

  @include toDesktop {
    width: 64px;
    height: 56px;
  }

  @include toLabtop {
    width: 44px;
    height: 38px;
  }
}

.img-decor {
  @include size(294px);

  border-radius: 100%;
  overflow: hidden;
  background-image: url("../images/decor/decore_2.jpg");
  mix-blend-mode: overlay;
  background-size: cover;

  @include toDesktop {
    @include size(122px);

    transform: translateY(40px);
    margin-right: 74px;
  }

  @include toLabtop {
    @include size(194px);

    margin: auto;
  }
}

// /* Other */

.block-circle {
  $origin: &;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  position: relative;

  @include size(834px);

  padding: 42px;
  border-radius: 100%;
  background-color: var(--white);

  &__image {
    margin-bottom: 60px;
  }

  &__image-2 {
    @include toPhone {
      height: 90px;
      align-self: flex-end;
      margin-top: -30px;
    }
  }

  &__container {
    @include flex-center;
    flex-direction: column;
    text-align: center;
  }

  &__text {
    font-size: 19px;
    line-height: 87%;
    letter-spacing: 0.02em;
  }

  &__title {
    font-family: var(--second-family);
    font-size: 45px;
    line-height: 100%;
    letter-spacing: 0.02em;
    color: var(--dark-blue);
  }

  @include toLabtop {
    @include size(712px);

    &__title {
      font-size: 36px;
    }

    &__image {
      height: 50px;
    }
  }

  @include toPhone {
    position: relative;
    margin-top: 200px;
    width: 100%;
    height: max-content;
    border-radius: 30px;
    padding: 0 15px;

    &::after {
      @include size(500px);
      content: "";
      display: block;
      background: var(--white);
      position: absolute;
      top: 0%;
      transform: translateY(-20%);
      z-index: -1;
      border-radius: 100%;
    }

    &__image {
      margin-bottom: 14px;
      height: 70px;
    }

    &__decor {
      margin-bottom: 38px;
    }

    &__text {
      font-size: 16px;
      margin-bottom: 12px;
    }

    &__title {
      font-size: 21px;
    }
