.section-main-slider {
  position: relative;
  height: 770px;

  &__wrapper {
    position: absolute;
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding-block: 60px 115px;
    padding-left: 90px;

    transition: all 0.6s;

    &--hidden {
      @include no-event;
      opacity: 0;
    }
  }

  &__decor-1 {
    position: absolute;
    top: 0;
    left: 0;
  }

  &__decor-2 {
    position: absolute;
    bottom: 0;
    left: 30%;
  }

  &__content {
    position: relative;
  }

  &__switch {
    max-width: max-content;
    margin-bottom: 30px;

    &__line {
      position: relative;
      display: flex;
      flex-direction: column;
      justify-content: center;
      height: 12px;
    }

    &__point {
      @include size(12px);

      position: absolute;
      border-radius: 100%;
      background-color: #ea8529;

      top: 0;
      left: 18%;
      z-index: 1;

      transition: all 0.6s cubic-bezier(0.64, -0.41, 0.39, 1.42);

      &--right {
        left: 78%;
        background-color: #399df5;
      }
    }

    &__container {
      display: flex;
      align-items: end;
      gap: 30px;
      margin-bottom: 12px;
    }

    &__item {
      @include flex-center;
      flex-direction: column;
      gap: 4px;

      font-family: var(--font-family);
      font-weight: 400;
      font-size: 18px;
      letter-spacing: 0.01em;
      color: #5d5955;

      cursor: pointer;
      transition: all var(--transition-time);
      border: 2px solid transparent;
      border-bottom: none;
      border-inline: none;
      border-radius: 35px;

      &:hover {
        border-color: var(--white);
        transform: scale(1.05) translateY(-1px);
        background: radial-gradient(
          circle,
          rgb(255 255 255 / 30%) 1%,
          rgb(0 0 0 / 0%) 60%
        );

        filter: drop-shadow(0 0 2px var(--white))
          drop-shadow(0 0 8px var(--white)) drop-shadow(0 0 16px var(--white));
      }

      & > img {
        @include size(70px);
        object-fit: contain;
        opacity: 0;
        transform: scale(0.75);
        transition: all var(--transition-time);
      }

      &--active {
        color: #ea8529;

        & > img {
          opacity: 1;
          transform: scale(1);
        }

        &-right {
          color: #399df5;

          & > img {
            opacity: 1;
            transform: scale(1);
          }
        }
      }
    }

    &__hr {
      height: 24px;
      width: 1px;
      background-color: var(--text-talee-title);
      opacity: 0.3;
    }
  }

  &__picture {
    @include flex-center;
    position: relative;

    &__info-container {
      @include no-event;
      @include size(100%);
      position: relative;
      z-index: 1;

      & > * {
        position: absolute;
        pointer-events: all;
      }
    }

    &__info {
      &--1 {
        top: 15%;
        left: 38%;
      }

      &--2 {
        top: 36%;
        left: 54%;
      }

      &--3 {
        top: 77%;
        left: 59%;
      }

      // ---

      &--4 {
        top: 63%;
        left: 28%;
      }

      &--5 {
        top: 80%;
        left: 53%;
      }

      &--6 {
        top: 45%;
        left: 67%;
      }
    }
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 84px;
    line-height: 110%;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: #62500f;

    & b {
      font-family: var(--font-family);
      font-weight: 600;
      font-size: 84px;
      line-height: 101%;
      letter-spacing: 0.02em;
      text-transform: uppercase;
      color: var(--prinary);
    }
  }

  &__container {
    display: flex;
    gap: 24px;
    padding-right: 36px;
  }

  &__text {
    width: 100%;
    max-width: 420px;
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 22px;
    line-height: 120%;
    color: #5d5955;

    & span {
      font-weight: 700;
      color: #ea8529;
    }
  }

  &__button {
    width: 100%;
    place-self: end;
  }
}

