.map {
  position: relative;
  margin: auto;
  width: 1168px;
  height: 790px;
  cursor: grab;

  &:active {
    cursor: grabbing;
  }

  &__menu {
    position: relative;
    place-self: left;
    overflow: hidden;

    border: 2px solid var(--surface-white-30);
    border-radius: 20px;
    width: 764px;
    height: calc(100dvh - 100px);
    max-height: 920px;

    -webkit-backdrop-filter: blur(8px);
    backdrop-filter: blur(8px);
    box-shadow: 0 0 14px 0 rgba(180, 174, 167, 0.08);
    background: rgba(255, 255, 255, 0.75);
    z-index: 2;

    display: grid;
    grid-template-rows: 1fr max-content;

    transition: var(--transition-time);

    &--hidden {
      @include no-event;
      opacity: 0;
      transform: translateX(-100px);
    }
  }

  &__container {
    @include no-event;
    @include fullscreen;
    z-index: 1;
  }

  &__list {
    @include size(100%);
    position: relative;
  }

  &__item {
    display: flex;
    align-items: start;
    gap: 6px;
    position: absolute;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 21px;
    line-height: 110%;
    text-transform: uppercase;
    color: var(--surface-white-80);
    border-inline: 10px solid transparent;
    transform: translateX(-10px);

    &::before {
      @include size(10px);
      content: "";
      display: inline-block;
      margin-top: 6%;
      border-radius: 50%;
      background-color: var(--surface-white-80);
    }

    &--active {
      color: var(--prinary);
      transition: var(--transition-time);
      background-color: #FBE7D4;
      border-color: #FBE7D4;

      border-radius: 4px;
  
      &::before {
        @include size(10px);
        content: "";
        display: inline-block;
        margin-top: 6%;
        border-radius: 50%;
        background-color: var(--prinary);
      }
    }
  }

  &__cursor {
    @include no-event;
    position: fixed;
    font-size: 18px;
    padding: 2px;
    z-index: 999;
    display: none;
    color: #fbe7d4;
    text-shadow: 1px 1px 3px rgb(50 50 50 / 100%);

    &::before {
      content: "";
      width: 0;
      height: 0;
      border-top: 8px solid transparent;
      border-right: 8px solid var(--prinary);
      border-bottom: 8px solid transparent;
      position: absolute;
      border-radius: 50px;
      left: -10px;
      top: 2px;
      filter: drop-shadow(0 1px 1px rgb(50 50 50 / 50%))
    }
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 48px;
    line-height: 120%;
    text-transform: uppercase;
    color: var(--text-talee-accent);
  }

  & svg {
    filter: drop-shadow(0 0 20px #ea8629a9);
    position: absolute;
    top: 0;
    left: 0;
  }

  & path {
    transition: opacity 0.3s, stroke-width 0.3s;
  }
}

