.button-info {
  @include flex-center;

  position: relative;
  border-radius: 100%;
  cursor: pointer;

  transition: all var(--transition-time);

  &:hover {
    filter: drop-shadow(0 0 10px var(--surface-white-80));
  }
  
  &__icon {
    @include flex-center;
    @include size(56px);
    padding: 23px;
    border-radius: 100%;
    background-color: var(--surface-white-80);
    box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.21);
    z-index: 2;

    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
  }
  
  &__line {
    position: absolute;
    background-color: #b4aea7;;
    height: 3px;
    width: 20px;
    border-radius: 2px;
    transition: var(--transition-time);
    
    &--plus {
      transform: rotate(90deg);
    }
  }

  &__pop-up {
    @include shadow;
    position: absolute;
    bottom: 65%;
    right: 65%;
    transform-origin: right bottom;

    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);

    border-radius: 16px;
    background: var(--surface-white-80);
    padding: 15px 20px 20px;
    width: 100%;
    min-width: 232px;
    z-index: 1;

    transition: all var(--transition-time);

    &__title {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 15px;
      line-height: 100%;
      text-transform: uppercase;
      color: #62500f;
      margin-bottom: 12px;
    }

    &__text {
      font-family: var(--second-family);
      font-weight: 400;
      font-size: 15px;
      line-height: 100%;
      color: #b4aea7;

      & > b {
        color: #5d5955;
      }
    }

    &--hidden {
      transform: scale(0);
      opacity: 0;
    }
  }
}
