.header {
  @include gridSetting;

  $origin: &;

  align-items: center;
  border: 1px solid rgba(180, 174, 167, 0.21);
  border-radius: 0 0 18px 18px;
  padding: 0 30px;

  // box-shadow: 0 4px 10px 0 #96b6dc4d;
  // background: #ffffffe6;

  // -webkit-backdrop-filter: blur(16px);
  // backdrop-filter: blur(16px);

  -webkit-backdrop-filter: blur(18px);
  backdrop-filter: blur(18px);
  box-shadow: 0 4px 11px 0 rgba(180, 174, 167, 0.15);
  background: rgba(255, 253, 248, 0.8);

  &__logo {
    // grid-column: 1/1;
  }

  &__container {
    height: 100%;
    // grid-column: 2/5;
    display: flex;
    align-items: center;
    gap: clamp(100px, 3vw, 10%);
  }

  &__navigation {
    width: 45%;
  }

  &__button {
    // grid-column: 5/7;
    justify-self: end;
    max-width: 214px;
    width: 100%;
  }

  &__slide-menu {
    // display: none;
  }

  &__link {
    position: relative;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    &::before {
      content: "";
      display: block;
      height: 4px;
      background-color: var(--light-blue);
      border-radius: 6px;

      width: 0%;
      position: absolute;
      bottom: 0;
      left: 0;
      
      transition: width var(--transition-time);
    }

    &--active::before {
      content: "";
      width: 100%;
    }
  }

  @include toDesktop {
    padding: 0 20px;
    display: flex;
    justify-content: space-between;

    &__logo {
      width: 184px;
      padding: 0;
    }

    &__container {
      justify-content: space-around;
      gap: none;
    }
  }

  @include toLabtop {
    height: 70px;

    &__navigation {
      display: none;
    }

    &__slide-menu {
      display: block;
    }
  }

  @include toPhablet {
    &__link {
      display: none;
    }

    &__button {
      display: none;
    }
  }

  @include toPhone {
    padding: 0 15px;

    &__logo {
      width: 140px;
    }
  }
}


.button-menu {
  @include flex-center;
  @include min-size(44px);
  @include max-size(44px);
  place-self: center;

  padding: 10px;
  border-radius: 100%;
  border: 1px solid var(--white);
  box-shadow: 0 3px 4px 0 rgba(31, 33, 29, 0.11);
  background-color: var(--prinary);
  cursor: pointer;
  transition: all var(--transition-time);

  &:hover {
    transform: scale(1.1);
  }

  @include toPhone {
    @include min-size(38px);
    padding: 5px;
  }
}
