.splide {
  $origin: &;
  filter: drop-shadow(0 3px 10px rgb(31 33 29 / 14%));
  margin-bottom: 50px;
  height: max-content;

  $transform-translate-y: 20px;

  &__slide {
    cursor: pointer;
  }

  &__track {
    & article {
      height: 100%;
    }
  }

  &__arrow {
    @include size(40px);
    background-color: var(--white);
    border: 1px solid var(--light-blue);
    top: 100%;
    bottom: inherit;
    transform: translateY($transform-translate-y - 6);
    z-index: 1;

    & > svg {
      fill: var(--light-blue);
    }

    transition: background-color 0.1s, box-shadow 0.2s, border 0.2s,
      opacity 0.2s;

    @include onLabtop {
      &:hover {
        opacity: 1;
        box-shadow: 0 0 40px -5px var(--light-blue);
      }

      &:active {
        transform: translateY($transform-translate-y - 6) scale(0.9);
        background-color: var(--light-blue);
        color: var(--white);
        border: 6px solid var(--light-blue);

        & > svg {
          fill: var(--white);
        }
      }
    }
  }

  &__item {
    border-radius: 20px;
    object-fit: contain;
    min-height: 300px;
    background-color: rgb(128 128 128 / 12.8%);
  }

  &__pagination {
    transform: translateY($transform-translate-y);
    gap: 10px;
    bottom: inherit;
    top: 100%;

    &__page {
      @include size(10px);
      border: 1px solid var(--light-blue);
      border-radius: 100%;
    }

    @include toLabtop {
      transform: translateY($transform-translate-y) scale(0.75);
    }
  }

  .splide__pagination__page.is-active {
    background-color: var(--light-blue);
    transform: scale(1.3);
  }

  // -------------------------------------------------------------

  &--fix {
    margin-right: 33px;

    #{$origin}__slide {
      margin-inline: 11px !important;

      & > * {
        width: calc(100%) !important;
        max-width: calc(100%) !important;
      }
    }

    @include toTablet {
      margin-right: 19px;

      #{$origin}__slide {
        margin-inline: 0 !important;
        margin-right: 10px !important;

        & > * {
          height: 100%;
        }
      }
    }

    @include toPhone {
      margin-right: 0;

      #{$origin}__slide {
        min-height: 300px;
      }
    }
  }
}
