.link {
  $origin: &;
  display: inline-flex;
  align-items: center;
  cursor: pointer;

  &__text {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 100%;
    letter-spacing: 0.01em;
    text-transform: uppercase;
    color: var(--text-talee-title);

    padding-bottom: 4px;
    border-bottom: 1px solid var(--text-talee-title-50);
    transition: all var(--transition-time);
  }

  &__icon {
    place-self: start;
    margin-left: 4px;
    transition: all var(--transition-time);
    
    & svg {
      @include size(14px);
    }

    & > * * {
      stroke: var(--text-talee-title);
      transition: all var(--transition-time);
    }
  }

  &:hover {
    #{$origin}__text {
      color: var(--prinary);
      border-bottom: 1px solid var(--prinary-50p);
    }

    #{$origin}__icon > * * {
      stroke: var(--prinary);
    }
  }

  &--light {
    #{$origin}__text {
      // font-size: 14px;
      color: var(--surface-white-80);

      border-bottom: 1px solid var(--surface-white-30);
    }

    #{$origin}__icon {
      & > * * {
        stroke: var(--surface-white-80);
        transition: all var(--transition-time);
      }
    }
  }

  &--negative {
    #{$origin}__text {
      color: var(--prinary);
      border-bottom: 1px solid var(--prinary-50p);
    }

    #{$origin}__icon {
      & > * * {
        stroke: var(--prinary);
      }
    }

    &:hover {
      #{$origin}__text {
        color: var(--text-talee-title);
        border-bottom: 1px solid var(--text-talee-title-50);
      }

      #{$origin}__icon {
        & > * * {
          stroke: var(--text-talee-title);
        }
      }
    }
  }

  &--nowrap {
    text-wrap: nowrap;
  }
}
